/*
Note: any .css or .scss files included in the 'styles' directory
will be correctly compiled during `gulp serve` and `gulp`
 */

@import "variables";
@import "mixins";

@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,400i,600,600i");

html {
  font-size: 16px;
}

html, body {
  font-family: 'Open Sans', sans-serif;
}

a {
  color: $blue;
  transition: all 150ms ease;

  &:hover {
    color: lighten($blue, 20%);
    transition: all 150ms ease;
  }
}

// Remove spinners on inputs
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.container {
  max-width: 700px;
}

.header {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  padding: 40px 0;

  &.header--centred {
    justify-content: center;
  }

  .header__link {
    font-size: .8rem;
  }
}

.steps {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 1rem 0 2rem;

  @include breakpoint(xs) {
    display: none;
  }


  .steps__item {
    padding: 0 .5rem;
    font-size: .8rem;
    color: $blue;
    opacity: .5;

    .fa {
      margin-left: .5rem;
    }

    &.steps__item--current {
      opacity: 1;
    }

    &.steps__item--previous {
      color: desaturate($blue, 75%);
    }
  }
}

.form-header {
  text-align: center;
  margin-bottom: 3rem;
  padding: 2rem 2rem 0 2rem;

  @include breakpoint(xs) {
    padding: 1rem 1rem 0 1rem;
  }
}

.form-content {
  padding: 0 2rem;

  @include breakpoint(xs) {
    padding: 0 1rem;
  }
}

.form-footer {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.registration-body {
  border: 1px solid $snow;
  border-radius: 4px;
  margin-bottom: 5rem;
}

h1 {
  color: $blue;
  font-size: 2rem;
  margin: .5rem 0 2rem 0;
  font-weight: 600;
  letter-spacing: -.5px;
}

h2 {
  color: $blue;
  font-size: 1.25rem;
  margin: 2rem 0;
}

p {
  font-size: 1rem;
  line-height: 1.5;
  color: $charcoal;
}

.form-group {}

.form-control-label {
  margin-bottom: .5rem;
  color: $black;

  &.no-margin-bottom {
    margin-bottom: 0;
  }
}

.form-control {
  color: $black;
  border: 2px solid $grey;
  padding: .75rem;

  &:focus {
    border-color: $blue;
  }

  &.form-control--count {
    width: 60px;
  }
}

select.form-control:not([size]):not([multiple]) {
  height: calc(2.75rem + 4px);
}

.faded {
  color: lighten($charcoal, 40%);
}

.mouseprint {
  font-size: .8rem;
  color: $charcoal;
}

.btn-primary {
  color: $white;
  font-weight: 600;
  padding: .75rem;
  margin-bottom: 1rem;
  box-shadow: $btn-box-shadow;

  &:disabled {
    background-color: lighten(desaturate($blue, 30%), 30%);
    border-color: lighten(desaturate($blue, 30%), 30%);
    box-shadow: none;
  }
}

.password-strength {
  margin-bottom: 1rem;

  .password-strength__label {
    font-size: .8rem;
    color: $charcoal;
  }

  .password-strength__value {
    font-weight: 600;
    font-size: .8rem;

    &.password-strength__value--weak {
      color: $red;
    }

    &.password-strength__value--okay {
      color: $orange;
    }

    &.password-strength__value--strong {
      color: $green;
    }
  }
}

.subscriptions {
  border: 1px solid $snow;
  border-radius: 4px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;

  @include breakpoint(xs) {
    flex-wrap: wrap;
    flex-direction: column;
  }


  .panel {
    padding: 1.5rem 1rem;

    @include breakpoint(xs) {
      flex: 1 1 auto;
    }


    &.panel--left {
      flex: 1 1 66.66%;

      .form-row {
        margin-bottom: 1rem;
        align-items: center;

        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }

    &.panel--right {
      flex: 1 1 33.33%;
      background-color: lighten($snow, 0.5);
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }
  }
}

.cost {
  .dollar-sign {
    font-size: 1rem;
    top: -1rem;
    position: relative;
    line-height: 1;
    color: $blue;
  }

  .amount {
    font-size: 2.5rem;
    line-height: 1;
    letter-spacing: -1.5px;
    color: $blue;
  }

  .per-month {
    font-size: 1.5rem;
    line-height: 1;
    color: $blue;
  }
}

.subscription-count {
  font-weight: 600;
}

.total-subscriptions {
  text-align: center;
  color: lighten($charcoal, 30%);
  font-size: .8rem;
  line-height: 1.25;
}

.compare-plans {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  border-top: 1px solid $snow;
  border-bottom: 1px solid $snow;
  margin-bottom: 2rem;

  @include breakpoint(xs) {
    flex-direction: column;
    flex-wrap: wrap;
  }


  .comparison-item {
    border-right: 1px solid $snow;
    padding: 2rem;

    @include breakpoint(xs) {
      border-right: 0;
      padding: 2rem 1rem 0 1rem;
    }


    &:last-of-type {
      border-right: 0;
    }

    .comparison-item__header {
      h2 {
        font-size: 1.25rem;
        font-weight: 600;

        @include breakpoint(xs) {
          margin-top: 0;
        }
      }

      .amount,
      .dollar-sign,
      .per-month {
        font-weight: 600;
      }
    }

    .comparison-item__content {
      margin-top: 1rem;
      padding: 1rem;

      li {
        font-size: .75rem;
        margin-bottom: .5rem;
        line-height: 1.25;

        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }
  }
}

.form-divider {
  margin: 2rem 0;

  .alternate {
    font-size: .7rem;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: $blue;
    line-height: 1;
    background-color: $white;
    padding-right: 1rem;
    position: absolute;
    top: 27px;
  }
}

.billing-mode {
  margin-bottom: 2rem;

  .tabs {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    border-bottom: 1px solid $snow;
    padding: 0;

    li {
      margin-right: 2rem;
      margin-bottom: -1px;
      color: $blue;
      opacity: .6;
      list-style: none;
      padding-bottom: 1rem;
      transition: all 150ms ease;

      @include breakpoint(xs) {
        text-align: center;
      }


      &.active {
        opacity: 1;
        border-bottom: 3px solid $blue;
      }

      &:hover {
        cursor: pointer;
        opacity: 1;
      }

      &:last-of-type {
        margin-right: 0;
      }
    }
  }
}